
// Pobranie referencji do modala i przycisku zamykajcego
const myImage = document.getElementById('myImage');
const modal = document.getElementById('myModal');
const closeButton = document.getElementById('closeButton');


// Funkcja otwierajca modal
function openModal() {
    modal.style.display = 'flex';
  }
// Funkcja do zamykania modala
function closeModal() {
  modal.style.display = 'none';
}

// Nasuchiwanie kliknicia w obrazek
myImage.addEventListener('click', openModal);
// Nasuchiwanie kliknicia na przycisk zamykajcy
closeButton.addEventListener('click', closeModal);

// Nasuchiwanie kliknicia na zewntrzn cz modala
window.addEventListener('click', function(event) {
  if (event.target === modal) {
    closeModal();
  }
  
}

);
document.addEventListener('keydown', function(event) {
    if (event.key === 'Escape') {
      closeModal();
    }
  });
